.voice_link_list_item._cover:before {
  position: absolute;
  display: inline-block;
  content: "";
  background-color: #282828;
  z-index: 10;
  width: 100%;
  height: 100%;
  top: 0;
  opacity: 0.5;
}
.voice_link_list_item:not(._cover) .item_link:after {
  position: absolute;
  display: inline-block;
  content: "";
  background-color: #FFF;
  z-index: 10;
  width: 100%;
  height: 100%;
  top: 0;
  transition: all 0.4s ease;
  opacity: 0;
}
@media screen and (min-width: 1080px) {
  .voice_link_list_item:not(._cover) .item_link:hover:after {
    opacity: 0.4;
  }
}